		<div class="article__layout article__img">
			<img src="@/assets/images/article.png" v-show="!value.showHoverImage">
			<img src="@/assets/images/article_hov.png" v-show="value.showHoverImage">
		</div>

		<div class="article__layout article__content">
			<div class="article__date">2016.01.01</div>
			<h3 class="article__title">サンプルテキストサンプルテキストサンプルテキストサンプルテキストサンプルテキスト</h3>
			<div class="article__desc">
				サンプルテキストサンプルテキストサンプルテキストサンプルテキストサンプルテ <br/>
				キストサンプルテキストサンプルテキストサンプルテキスキストサンプルテキス ...
			</div>
		</div>
	</article>  <!-- // article -->
</template>

<script>
import Sidebar from '@/components/common/sidebar/Sidebar'

export default {
  name: 'app-article',
  components: { Sidebar },
	props: {
		value: {}
	}
}
</script>



